* {
  box-sizing: border-box;
}

.container-div {
  /*border: 5.5px solid rgb(100, 98, 98);*/
  font-family: 'PT Serif', serif;
  font-size:16px;
  text-align: left;
  width: 100%;
}

.App-header {
  background: rgb(245,242,242);
  background: linear-gradient(45deg, rgba(245,242,242,1) 35%, rgba(242,224,235,1) 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.bar-div {
  width: 70%;
  margin: auto;
  display: flex;
  padding: 1.5rem;
}

.logo-div {
  flex-basis: 13%;
  /*text-align: left;*/
}

.logo {
  width:7.5rem;
  height: auto;
  border:none;
}

nav {
  flex-basis: 87%;
  text-align: right;
  flex-flow: row nowrap;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: right;
  font-size: 1.05em;
  font-weight: bold;
}

nav li {
  display: inline-block;
}

nav a {
  text-decoration:none;
  color: rgb(44, 40, 40);
  display: block;
  padding:1.2em 0 0;
  margin: 0 1rem;
  line-height: 1.1em;
}

nav a:hover {
  border-bottom: 0.1em solid orange;
}

.active {
  font-size:1.1em;
  border-bottom: 0.1em solid orange;
}

.burger-icon {
  width: 2rem;
  border: none;
  padding:1.2em 0 0;
  margin: 0 2rem -4rem 0;
  display: none;
}

@media only screen and (max-width: 1200px) {
  .bar-div {
    width: 90%;
    margin: 1rem auto;
  }
}

@media only screen and (max-width: 740px) {
  .bar-div{
    justify-content: space-between;
  }

  .nav-menu {
    display: none;
  }

  .burger-menu {
    padding: 0.7rem 0.2rem;
  }

  .burger-icon {
    display: block;
  }

  .nav-menu.show {
    display: block;
    position: absolute;
    margin: 4rem 0 1rem 0;
    right: 20px;
    background-color: rgba(255,255,255,.9);
    border-radius: 4px;
  }
  .nav-menu ul {
    margin: 1rem;
  }

  .nav-menu li {
    padding: 13px 20px;
    text-decoration: none;
    display: block;   
  }

  .nav-menu a {
    display: inline;
    padding: 0.2em 0 0;
  }

  .active {
    font-size: 1em;
    border-bottom: 0.1em solid orange;
  }
}