.intro-text{
  margin: 2% 7%;
}

.intro-text p{
  color: black;
  font-size: 1.2rem;
  font-weight: 600;
  color: rgb(163, 162, 162);
}

.body-wrap{
  display: flex;
}

.skills-cv{
  display: inline-block;
  text-align: left;
  flex-basis: 42%;
  margin-left: 7%;
}

.badge-row{
  display: flex;
  flex-flow: row wrap;
}

.badge {
  padding: 0.1rem 0.5rem;
  margin: 0.2rem 0.2rem;
  background-color: white;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 5px;
}

.resume-logos{
  position: relative;
  width: 15rem;
  margin-top: 4rem;
}

hr{
  position:absolute;
  width: 100%;
  border: 2px rgb(250, 214, 131) solid;
  margin-top: 1.9rem;
}
/* border: 2px rgb(250, 214, 131) solid;*/

.logos-home{
  position: relative;
  list-style: none;
  display: flex;
  flex-flow: row nowrap;
  margin-top: 14%;
  padding-inline-start: 0px;
  max-width: 18rem;
}

.logo-home{
  flex-basis: 34%;
  justify-content: space-between;
  margin: 4% auto; 
  z-index: 2;
  background: rgb(247, 240, 232);
  max-width: 39px;
}

.logo-home.res{
  max-width: 30px;
}

.logo-home .img-res{
  border-radius: 0px;
  max-width: 32px;
  height: 39px; 
}

.logo-home img {
  border:none;
  width: 2.5rem;
  opacity: 0.7;
  border-radius: 10px;
}

.logo-home:hover img {
  opacity: 1;
}

.microodles-div{
  width: 23%;
  margin: auto;
}

.microodles{
  border: none;
  margin: -15px 0px 83px -15px;
  opacity: 0.65;
}

.microodles:hover {
  cursor: pointer;
  opacity: 1;  
}

@media only screen and (max-width: 1400px) {
  .box-profile{
    width: 80%;
  }
  .bar-div{
    width: 90%;
  }
  .intro-text{
    margin: 2% 7%;
  }
  .logos-home {
    margin-top: 3rem;
  }
}

@media only screen and (max-width: 1000px) {

}

@media only screen and (max-width: 740px) {
  .box-profile{
    width: 86%;
    height: 60%;
  }
  .microodles{
    visibility: hidden;
  }
  .skills-cv{
    display: inline-block;
    flex-basis: 100%;
    margin-top: -1rem;
  }
}

@media only screen and (max-height: 900px) {
  .box-profile{
    width: 60%;
    height: auto;
  }
  .intro-text{
    margin: 0% 7%;
  }
  .logos-home{
    margin-top: 9%;
  }
  .resume h2{
    visibility: hidden;
  }
  .cv-icon{
    margin-top:-3rem;
    max-width:3rem;  
  }
}

